//
// Navs
// --------------------------------------------------
// 模块：Tab标签
// 描述：模块标题
// --------------------------------------------------

.nav-tabs {
    > li {
        > a {
            border-bottom: 1px solid @nav-tabs-border-color;
            background-color: @nav-link-hover-bg;
            color: @gray;
            &:hover {
                border-color: transparent;
            }
        }
        &.active {
            > a, > a:focus {
                background-color: transparent;
            }
        }
    }
    &.nav-tabs-noborder {
        border-bottom: 0;
        > li {
            > a {
                border-bottom-color: transparent;
            }
            &.active {
                > a {
                    border-color: transparent;
                    color: @brand-primary;
                }
            }
        }
    }
    &.nav-tabs-noradius {
        > li {
            > a {
                border-radius: 0;
            }
        }
    }
    &.nav-tabs-n2 {
        > li {
            width: 50%;
            text-align: center;
        }
    }
    &.nav-tabs-md {
        > li {
            > a {
                padding: 7px 15px;
            }
        }
    }
    &.nav-tabs-primary {
        > li {
            &.active {
                > a {
                    background-color: @brand-primary;
                    color: @white;
                }
            }
        }
    }
}

// 类名：nav-tabs-title
.nav-tabs-title {
    border-bottom: 1px solid @nav-tabs-border-color;
    margin-top: @nav-tabs-title-margin-top;
    margin-bottom: @nav-tabs-title-margin-bottom;
    line-height: normal;

    &.nav-tabs-title-bg {
        background-color: @nav-tabs-title-bg-color;
        padding-right: 5px;
    }
    &.nav-tabs-noborder {
        border-bottom: 0;
        margin-top: 0;
        margin-bottom: 0;
        &.mt-10 {
            margin-top: 10px !important;
        }
        > li > label {
            position: relative;
            display: block;
            padding: 5px 0;
            margin-bottom: 0;
        }
    }

    > li {
        float: left;
        // Make the list-items overlay the bottom border
        //margin-bottom: -1px;
        font-size: @font-size-14;
        // tab 形式模块标题
        > span {
            position: relative;
            display: block;
            padding: @nav-tabs-title-active-padding;
            &.dline {
                margin-bottom: -1px;
                border-bottom: 2px solid @brand-primary;
            }
        }
        &.titles {
            color: white;
        }
        &.default{
            a {
                padding: @nav-tabs-title-default-padding;
                border-radius: @border-radius-base @border-radius-base 0 0;
                margin-left: 5px;
                //margin-bottom: -1px;
                background-color: @nav-link-hover-bg;
                color: @gray;
                &:hover,
                &:focus {
                    background-color: @nav-tabs-title-active-bg-color;
                    color: @white;
                }
            }
            &:first-child {
                a {
                    margin-left: 0;
                }
            }
            &.default-sm {
                span {
                    padding: @nav-tabs-title-margin-top;
                }
            }
        }
        &.active > span,
        &.active > a {
            padding: @nav-tabs-title-link-padding;
            border-radius: @border-radius-base @border-radius-base 0 0;
            background-color: @nav-tabs-title-active-bg-color;
            color: @white;
        }
        &.active > span.bg-green,
        &.active > a.bg-green {
            background-color: @nav-tabs-title-active-bg-color-green;
        }
        &.active > span.only {
            border-bottom: 2px solid @nav-tabs-title-active-link-hover-border-color;
            background-color: transparent;
            color: @brand-primary;
        }
        &.buttons {
            float: right;
            //.btn {
            //margin-top: @nav-tabs-title-btns-margin-top;
            //}
            // a链接形式按钮
            a {
                color: @gray-light;
            }
            > a.btn {
                display: inline-block;
                .button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; @btn-border-radius-base);
                &.btn-sm {
                    // line-height: ensure proper height of button next to small input
                    .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small);
                }
                // Active state, and its :hover to override normal :hover
                &.btn-default {
                    .button-variant(@btn-default-color; @btn-default-bg; @btn-default-border);
                }
                &.btn-primary {
                    .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
                }
                // Success appears as green
                &.btn-success {
                    .button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);
                }
                // Info appears as blue-green
                &.btn-info {
                    .button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);
                }
                // Warning appears as orange
                &.btn-warning {
                    .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border);
                }
                // Danger and error appear as red
                &.btn-danger {
                    .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
                }
            }
            .btn-group {
                .dropdown-menu {
                    min-width: 100px;
                    > li {
                        > a {
                            padding-left: 10px;
                            padding-right: 10px;
                        }
                    }
                }
            }
        }
    }
    // pulling this in mainly for less shorthand
    &.nav-justified {
        .nav-justified();
        .nav-tabs-justified();
    }
}

.nav-tabs-multi {
    > li {
        &.active {
            > a, > a:focus {
                background-color: @white;
            }
        }
    }
}

.nav-sm {
    > li {
        > a {
            padding: 5px 10px;
        }
    }
}
