//
// Navmain
// --------------------------------------------------
// 模块：横向多级导航
// 类名：navbar-main
// 描述：思诺博UI横向主导航
// --------------------------------------------------

.navbar-main {
    background-color: @navbar-main-bg;
    border-color: @navbar-main-border;
    &:extend(.clearfix all);

    &.navbar {
        margin-bottom: @navbar-main-margin-bottom;
        border-radius: 0;
    }

    .navbar-brand {
        padding: 10px 0 0 10px;
        color: @navbar-main-brand-color;
        &:hover,
        &:focus {
            color: @navbar-main-brand-hover-color;
            background-color: @navbar-main-brand-hover-bg;
        }
        img {
            width: 60%;
        }
    }
    @media (min-width: @screen-sm-min) {
        .navbar-brand {
            display: none;
        }
    }

    .navbar-text {
        color: @navbar-main-color;
    }

    .nav {
        position: relative;
        > li {
            position: static;
            margin-right: 1px;
            > a {
                @media (min-width: @screen-lg-min) {
                    padding: @navbar-main-link-padding;
                }
                @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
                    padding: @navbar-main-link-padding-md;
                }
                @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
                    padding: @navbar-main-link-padding-sm;
                }
                font-size: @font-size-16;
            }
        }
    }

    .dropdown-menu {
        width: @container-large-desktop;
        min-width: 100%;
        .menu-box {
            padding:0px 15px;
            @media (max-width: @screen-xs-max) {
                background-color: @white;
            }

            &:extend(.clearfix all);

            .nav-tabs {
                float: left;
                width: 160px;
                border-bottom: 0;
                > li {
                    display: block;
                    width: 100%;
                    margin-bottom: 1px;

                    // Actual tabs (as links)
                    > a {
                        margin-right: 0;
                        padding: @navbar-main-tabs-padding;
                        line-height: @line-height-base;
                        border: 1px solid transparent;
                        border-radius: 0;
                        font-size: @font-size-14;
                        color: @gray-dark;
                        &:hover {
                            border-color: @nav-tabs-link-hover-border-color @nav-tabs-link-hover-border-color;
                        }
                        i {
                            float: right;
                            margin-top: 3px;
                        }
                    }

                    // Active state, and its :hover to override normal :hover
                    &.active > a {
                        &,
                        &:hover,
                        &:focus {
                            color: @nav-tabs-active-link-hover-color;
                            background-color: @nav-tabs-link-hover-border-color;
                            border: 1px solid transparent;
                            cursor: default;
                        }
                    }
                }
            }
            .tab-content,
            .menu-content {
                float: right;
                width: 998px;
                @media (max-width: @screen-xs-max) {
                    width: ~'-moz-calc(100% - 170px)';
                    width: ~'-webkit-calc(100% - 170px)';
                    width: ~'calc(100% - 170px)';
                }
                h3 {
                    margin-top: 0;
                    margin-bottom: 10px;
                    font-size: @font-size-14;
                    font-weight: bold;
                }
                ul {
                    padding: 10px 15px 10px;
                    width: 50%;
                    display: inline-block;
                    float: left;
                    li {
                        a {
                            color: @gray-dark;
                        }
                    }
                    &.wbfb {
                        width: 100%;
                    }
                }
            }
            .menu-content {
                width: 100%;
            }
            .menu-content-box {
                ul {
                    li {
                        display: none;
                    }
                    &:hover {
                        li {
                            display: block;
                        }
                    }
                }
            }
        }
    }
    .dropdown-menu-wrap {
        width: 203px;
        min-width: auto;
        .menu-box {
            .menu-content {
                float: left;
                width: 100%;
                ul {
                    width: 100%;
                    padding: 0;
                }
            }
        }
        .navbar-menu {
            border-color: transparent;
            &.navbar {
                border-left: 0;
                border-right: 0;
                border-radius: 0;
            }
        }
        .dropdown-menu {
            width: auto;
        }
        .nav {
            > li {
                position: relative;
                > a {
                    padding: 10px 15px;
                }
            }
        }
        .navbar-nav {
            width: 100%;
            > li > a {
                line-height: @line-height-computed;
            }
        }
    }

    .navbar-nav {
        > li > a {
            color: @navbar-main-link-color;
            line-height: inherit;

            &:hover,
            &:focus {
                color: @navbar-main-link-hover-color;
                background-color: @navbar-main-link-hover-bg;
            }
        }
        > .active > a {
            &,
            &:hover,
            &:focus {
                color: @navbar-main-link-active-color;
                background-color: @navbar-main-link-active-bg;
            }
        }
        > .disabled > a {
            &,
            &:hover,
            &:focus {
                color: @navbar-main-link-disabled-color;
                background-color: @navbar-main-link-disabled-bg;
            }
        }
    }

    .navbar-toggle {
        border-color: @navbar-main-toggle-border-color;
        &:hover,
        &:focus {
            background-color: @navbar-main-toggle-hover-bg;
        }
        .icon-bar {
            background-color: @navbar-main-toggle-icon-bar-bg;
        }
    }

    .navbar-collapse,
    .navbar-form {
        border-color: @navbar-main-border;
    }

    .navbar-collapse {
        @media (max-width: @screen-xs-max) {
            padding-right: @navbar-padding-horizontal;
            padding-left:  @navbar-padding-horizontal;
        }
        @media (min-width: @screen-sm-min) {
            padding-left:  0;
            padding-right: 0;
        }
    }

    // Dropdown menu items
    .navbar-nav {
        // Remove background color from open dropdown
        > .open > a {
            &,
            &:hover,
            &:focus {
                background-color: @navbar-main-link-active-bg;
                color: @navbar-main-link-active-color;
            }
        }

        @media (max-width: @grid-float-breakpoint-max) {
            // Dropdowns get custom display when collapsed
            .open .dropdown-menu {
                > li > a {
                    color: @navbar-main-link-color;
                    &:hover,
                    &:focus {
                        color: @navbar-main-link-hover-color;
                        background-color: @navbar-main-link-hover-bg;
                    }
                }
                > .active > a {
                    &,
                    &:hover,
                    &:focus {
                        color: @navbar-main-link-active-color;
                        background-color: @navbar-main-link-active-bg;
                    }
                }
                > .disabled > a {
                    &,
                    &:hover,
                    &:focus {
                        color: @navbar-main-link-disabled-color;
                        background-color: @navbar-main-link-disabled-bg;
                    }
                }
            }
        }
    }


    // Links in navbars
    //
    // Add a class to ensure links outside the navbar nav are colored correctly.

    .navbar-link {
        color: @navbar-main-link-color;
        &:hover {
            color: @navbar-main-link-hover-color;
        }
    }

    .btn-link {
        color: @navbar-main-link-color;
        &:hover,
        &:focus {
            color: @navbar-main-link-hover-color;
        }
        &[disabled],
        fieldset[disabled] & {
            &:hover,
            &:focus {
                color: @navbar-main-link-disabled-color;
            }
        }
    }
}
