//
// topbar
// --------------------------------------------------
// 模块：头部
// 描述：页面头部
// --------------------------------------------------

.topbar {
    border-bottom: 1px solid @topbar-border-color;
    background-color: @topbar-bg-color;
    font-size: @font-size-12;

    span {
        line-height:29px;
        @media (max-width: @screen-xs-max) {
            &.topbar-right {
                width: 100%;
                padding-left: 15px;
            }
        }
    }
    a {
        color: @gray;
        &:hover {
            text-decoration: none;
        }
    }

    .btn-group {
        .btn {
            padding: 0 10px;
            background-color: transparent;
            border-radius: 0;
            z-index: 1003;
            color: @gray;
            border-top-width: 0;
            border-bottom-width: 0;
            text-decoration: none;
        }
        &.open {
            .dropdown-toggle {
                background-color: @white;
                border-left: 1px solid @dropdown-border;
                border-right: 1px solid @dropdown-border;
            }
        }
        .dropdown-menu {
            min-width: 220px;
            margin-top: -1px;
            border-radius: 0;
            z-index: 1002;
            > li {
                width: 50%;
                float: left;
                > a {
                    padding: 6px 10px;
                    color: @gray-light;
                }
            }
        }
    }
}

.header {
    padding: @header-padding-vertical @header-padding-horizontal;

    .logo {
        padding-top: 8px;
    }

    .search {
        padding-top: 14px;
        text-align: right;

        .nav-tabs-title {
            border-bottom: 0;
            margin-top: 0;
            margin-bottom: 0;
            > li.default {
                a {
                    padding: 5px 15px;
                    background-color: transparent;
                    &:hover,
                    &:focus {
                        color: @gray;
                    }
                }
            }
            > li.active > a {
                background-color: @nav-tabs-title-active-bg-color;
                &:hover,
                &:focus {
                    color: @white;
                }
            }
        }

        .input-group {
            .form-control {
                min-width: 360px;
                border-color: @brand-primary;
            }
            @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
                .form-control {
                    min-width: 200px;
                }
            }
        }
        .input-group-btn {
            .btn-default {
                border-color: @brand-primary;
            }
            .dropdown-menu {
                min-width: auto;
                margin-top: 0;
                padding: 0;
                > li {
                    &.divider {
                        margin: 0;
                    }
                    a {
                        padding: 6px 15px;
                    }
                }
            }
            &+.form-control {
                min-width: 280px;
            }
        }
        .input-group-addon {
            padding: 0;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            .btn {
                border-radius: 0 @border-radius-base @border-radius-base 0;
                padding: 6px 6px 7px 6px;
                &.btn-search{
                    background-color: #0085d1;
                    border-color: #0085d1;
                    padding: 6px 6px 7px 6px;
                }
            }
        }
        &.search-tabs {
            padding-top: 0;
            text-align: left;
            .input-group {
                position: relative;
                .fa-search {
                    position: absolute;
                    left: 12px;
                    top: 14px;
                    z-index: 3;
                    color: @gray-light;
                }
                .form-control {
                    height: 33px;
                    padding: 10px 6px;
                    border-width: 3px;
                    border-radius: 0;
                    text-indent: 20px;
                }
            }
            .input-group-addon {
                .btn {
                    padding: 8px 6px 9px 6px;
                    background-color: @brand-primary;
                    border-color: @brand-primary;
                    border-radius: 0;
                    font-size: @font-size-14;
                }
            }
        }
    }

    .shopcart {
        padding-top: 16px;

        .my-doing {
            margin-left: 20px;
        }

        .btn-group {
            .btn {
                position: relative;
                padding-left: 15px;
                padding-right: 15px;
                background-color: #f9f9f9;
                border-color: #dfdfdf;
                border-radius: 0;
                z-index: 1001;
                color: @gray;
                .box-shadow(none);
                > i {
                    margin-right: 5px;
                }
                .fa-2x {
                    font-size: 1.2rem;
                }
                .shopcart-name {
                    font-size: @font-size-14;
                }
                .shopcart-num {
                    position: absolute;
                    right: 20px;
                    bottom: 30px;
                    width: 20px;
                    height: 20px;
                    line-height: 20px;
                    background-position: 0 -44px;
                    opacity: 1;
                    color: @white;
                }
            }
            &.open {
                .dropdown-toggle {
                    border-left: 1px solid @dropdown-border;
                    border-right: 1px solid @dropdown-border;
                }
            }
            .dropdown-menu {
                min-width: 320px;
                margin-top: -1px;
                padding: 10px 0 0;
                border-radius: 0;
                &:extend(.clearfix all);

                .dropdown-menuname {
                    padding-left: 10px;
                    padding-bottom: 10px;
                    font-weight: bold;
                }

                .shopcart-list {
                    max-height: 356px;
                    margin-left: 10px;
                    overflow-y: auto;
                    &:extend(.clearfix all);

                    .shopcart-listitem {
                        border-top: 1px dotted @gray-light;
                        padding-top: 5px;
                        margin-top: 5px;
                        &:first-child {
                            margin-top: 0;
                            padding-top: 0;
                            border-top: 0;
                        }
                        &:extend(.clearfix all);
                        > h3 {
                            margin: 0;
                            padding: 8px;
                            background-color: @gray-lighter;
                        }
                        > ul {
                            padding: 5px 0 0;
                            > li {
                                float: left;
                                margin-left: 6px;
                                &.goods-thumb {
                                    width: 56px;
                                }
                                &.goods-name {
                                    width: 152px;
                                }
                                &.goods-attr {
                                    width: 56px;
                                }
                                &:first-child {
                                    margin-left: 0;
                                }
                                > span {
                                    display: block;
                                    text-align: right;
                                    &:first-child {
                                        color: @red;
                                    }
                                }
                                a {
                                    color: @gray-dark;
                                }
                            }
                        }
                    }
                }
                .shopcart-info {
                    padding: 5px 10px;
                    background-color: @gray-lighter;
                    &:extend(.clearfix all);
                    span {
                        line-height: 30px;
                    }
                    .btn {
                        padding-left: 10px;
                        padding-right: 10px;
                        background-color: @red;
                        color: @white;
                    }
                }
            }
        }
    }

    .info{
        .pull-right();
        .text-right();
        padding-top: 14px;
        span{
            i{
                font-size:28px;
                color: #0085d1;
            }
        }
        .info-state{
            .pull-right();
            .text-left();
            line-height: 15px;
            margin-left:8px;
            strong{
                font-size: @font-size-14;
            }
            span{
                .text-left();
                color: @gray;
                a{
                    color: @gray;
                    text-decoration: none;
                }
            }
        }
    }
}
