//
// Navmain
// --------------------------------------------------
// 模块：横向多级导航
// 类名：navbar-main
// 描述：思诺博UI横向主导航
// --------------------------------------------------

.navbar-menu {
    background-color: @navbar-menu-bg;
    border-color: @navbar-menu-border;

    &.navbar {
        border-radius: @border-radius-base @border-radius-base 0 0;
        margin-bottom: @navbar-menu-margin-bottom;
        @media (max-width: @screen-xs-max) {
            margin-bottom: 10px;
        }
    }

    .navbar-header {
        @media (min-width: @grid-float-breakpoint) {
            width: 100%;
            margin: -1px -1px 0 0;
        }
    }

    .navbar-brand {
        width: 100%;
        height: auto;
        color: @navbar-menu-brand-color;
        background-color: @navbar-menu-brand-hover-bg;
        border-radius: @border-radius-base @border-radius-base 0 0;
        @media (min-width: @screen-sm-min) {
            padding: @navbar-menu-brand-padding;
        }
        @media (max-width: @screen-xs-max) {
            float: inherit;
        }
        .btn-navbar-control{
           position: absolute;
            top:9.5px;
            right: 10px;
            cursor: pointer;
        }
    }

    .navbar-text {
        color: @navbar-menu-color;
    }
    .navbar-nav {
        > li {
            width: 100%;
            padding: 0px 0;
            border-top: 1px solid @navbar-menu-divider-border-color;
            > a {
                padding-top: 7px;
                padding-bottom: 7px;
                border: 1px solid transparent;
                color: @navbar-menu-link-color;
                font-size: @font-size-14;
                &[disabled]{
                    color:#d3d3d3;
                }
                &:hover,
                &:focus {
                    color: @navbar-menu-link-hover-color;
                    background-color: @navbar-menu-link-hover-bg;
                }
                > span:first-child,
                > i:first-child {
                    margin-right: 10px;
                }
                > span.pull-right,
                > i.pull-right {
                    margin-top: 3px;
                    margin-right: 0;
                    color: lighten(@gray-base, 69.5%);
                }
            }
            &:first-child {
                border-top: 0;
            }
        }
        > .active > a {
            &,
            &:hover,
            &:focus {
                color: @navbar-menu-link-active-color;
                background-color: @navbar-menu-link-active-bg;
            }
        }
        > .disabled > a {
            &,
            &:hover,
            &:focus {
                color: @navbar-menu-link-disabled-color;
                background-color: @navbar-menu-link-disabled-bg;
            }
        }
        // Remove background color from open dropdown
        > .open > a {
            &,
            &:hover,
            &:focus {
                position: relative;
                z-index: 1001;
                border-color: @dropdown-menu-border;
                border-right: 0;
                background-color: @dropdown-menu-bg;
                color: @navbar-menu-link-active-color;
            }
            > span.pull-right,
            > i.pull-right {
                color: @navbar-menu-link-active-color;
            }
        }
    }

    .navbar-toggle {
        border-color: @navbar-menu-toggle-border-color;
        &:hover,
        &:focus {
            background-color: @navbar-menu-toggle-hover-bg;
        }
        .icon-bar {
            background-color: @navbar-menu-toggle-icon-bar-bg;
        }
    }

    .navbar-collapse,
    .navbar-form {
        border-color: @navbar-menu-border;
    }
    .navbar-collapse {
        @media (max-width: @screen-xs-max) {
            padding-right: @navbar-padding-horizontal;
            padding-left:  @navbar-padding-horizontal;
        }
        @media (min-width: @screen-sm-min) {
            padding-left:  0;
            padding-right: 0;
        }
    }

    .dropdown-menu {
        top: 0px;
        left: 198px;
        min-width: auto;
        padding: 0;
        background-color: @dropdown-menu-bg;
        border: 1px solid @dropdown-menu-fallback-border; // IE8 fallback
        border: 1px solid @dropdown-menu-border;
        border-radius: 0;

        &.dropdown-menu-up {
            top: auto;
            bottom: 5px;
        }

        .menu-box {
            width: @dropdown-menu-box-width;
            //max-height: 408px; overflow-y: auto;      // 启用滚动条时，div.menu-box增加scrollbar-hover类名
            padding: @dropdown-menu-box-padding;
            &:extend(.clearfix all);

            ul {
                padding-left: 0;
            }
            .menu-content {
                ul {
                    h3 {
                        margin-top: 15px;
                        margin-bottom: 8px;
                        font-size: @font-size-14;
                        font-weight: bold;
                        a {
                            text-decoration: none;
                            &[disabled]{
                                color:#d3d3d3;
                                cursor: text;
                                text-decoration: none;
                            }
                        }
                    }
                    li {
                        a {
                            color: @gray-dark;
                            display: inline-block;
                            white-space: nowrap;

                            &[disabled]{
                                color:#d3d3d3;
                                cursor: text;
                                text-decoration: none;
                            }
                        }
                        &.menu-default,
                        &.menu-auto{
                            padding-top: 10px;
                        }
                        &.menu-auto {
                            white-space:nowrap;
                        }
                        .pipe {
                            color: lighten(@gray-base, 69.5%);
                        }
                    }
                    &:first-child {
                        h3 {
                            margin-top: 12px;
                        }
                    }
                }
            }
            &.menu-box-auto {
                width: auto;
                padding-right: 15px;
            }
            .menu-content-box {
                ul {
                    li {
                        display: none;
                    }
                    &:hover {
                        li {
                            display: block;
                        }
                    }
                }
            }
        }
    }

    // Links in navbars
    //
    // Add a class to ensure links outside the navbar nav are colored correctly.

    .navbar-link {
        color: @navbar-menu-link-color;
        &:hover {
            color: @navbar-menu-link-hover-color;
        }
    }

    .btn-link {
        color: @navbar-menu-link-color;
        &:hover,
        &:focus {
            color: @navbar-menu-link-hover-color;
        }
        &[disabled],
        fieldset[disabled] & {
            &:hover,
            &:focus {
                color: @navbar-menu-link-disabled-color;
            }
        }
    }
}
