//
// Scaffolding
// --------------------------------------------------
// 模块：脚手架&通用样式
// 描述：用于定义通用样式
// --------------------------------------------------

li {
    list-style: none;
}
i {
    font-style:normal
}
label {
    font-weight: normal;
}
input[type="radio"],
input[type="checkbox"] {
    //vertical-align:middle;
    //margin:-2px 5px 0 0;
    margin: 2px 0 0;
}
.radio,
.checkbox {
    margin-top: 0;
    margin-bottom: 0;
    label {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }
    span.error {
        display: none;
    }
}

.pipe {
    margin: 0 10px;
    color: lighten(@gray-base, 60%);
}
.text-white {
    color: @white;
}
.text-red {
    color: @red;
}
.text-orange {
    color: @orange;
}
.text-lightgray {
    color: lighten(@gray-base, 60%);
}
.text-weight {
    font-weight: bold;
}
.text-noweight {
    font-weight: 400;
}
.text-left {
    text-align: left !important;
}
.text-center {
    text-align: center !important;
}
.text-normal {
    font-style: normal;
}
.bg-transparent {
    background-color: transparent;
}
.bg-gray {
    background-color: @gray;
}
.bg-graylight {
    background-color: @graylight;
}
.f-14 {
    font-size: @font-size-14;
}
.f-18 {
