}
.septal-line {
    border-top: 1px solid @table-border-color;
    height: 0;
    font-size: 0;
    overflow: hidden;
}
.showStar:before {
    color: @red;
    vertical-align: top;
    content: "*";
}
.item-header .checkbox label {
