//
// login
// --------------------------------------------------
// 模块：登录
// 类名：login-wrap
// 描述：登录页
// --------------------------------------------------

.login-wrap {
    background-color: @login-bg-color;
    .container {
        @media (min-width: @screen-bg-min) {
            max-width: @container-lg;
            margin:0 auto;
        }
    }
    .login-banner {
        height: @login-banner-height;
    }
    .login-box {
        width: @login-box-width;
        margin-top: @login-box-margin-top;
        background: @white;
        border: 1px solid @login-box-bg-color;
        @media (min-width: @screen-sm-min) {
            float: right;
        }
        @media (max-width: @screen-xs-max) {
            margin-left: auto;
            margin-right: auto;
        }
        .login-title {
            padding: 15px 0;
            font-weight: 700;
            border-bottom: 1px solid @login-title-border-color;
            background-color: @login-title-bg-color;
            #gradient > .vertical(@start-color: @table-cell-th-start-color; @end-color: @table-cell-th-end-color; @start-percent: 0%; @end-percent: 100%);
        }
        .form-group {
            padding: @login-box-group-padding;
            .input-group {
                margin-top: @login-box-group-padding;
                &:first-child {
                    margin-top: 0;
                }
                .input-group-addon {
                    width: @login-box-addon-width;
                    line-height: @login-box-input-line-height;
                }
                .form-control {
                    width: @login-box-input-width;
                    height: @login-box-input-height;
                    line-height: @login-box-input-line-height;
                }
                .input-vcode {
                    width: @login-box-vcode-width;
                }
                .login-vcode {
                    padding: 0;
                    border: 0;
                    background-color: transparent;
                }
            }
        }
        .has-success {
            .form-control-validation(@input-color; @input-border; @input-bg);
        }
        .has-warning {
            .form-control-validation(@input-color; @input-border; @input-bg);
        }
        .has-error {
            .form-control-validation(@input-color; @input-border; @input-bg);
            .input-group-addon {
                background-color: @input-group-addon-bg;
            }
        }
        .login-btn {
            padding: 0 @login-box-group-padding 10px @login-box-group-padding;
            .btn {
                width: 100%;
                margin-bottom: 15px;
                padding: @login-btn-padding;
                font-size: @font-size-14;
            }
        }
        .login-error {
            margin: 10px @login-box-group-padding -15px @login-box-group-padding;
            display: none;
            .input-group-addon {
                display: block;
                width: 100%;
                padding: 3px 6px 3px 20px;
                text-align: left;
                border-color: transparent;
                background: transparent url("../lib/validation/images/error.gif") no-repeat left center;
            }
        }
    }
}

#login {
    .footer {
        margin-top: 0;
    }
}
