//
// Modals
// --------------------------------------------------

.modal-content {
  border-radius: @modal-border-radius;
}
.modal-header {
    background-color: @modal-header-bg-color;
}
.modal-title {
    font-size: @font-size-14;
}
.modal-header .close {
    opacity: .6;
    &:hover {
        opacity: .4;
    }
}
.modal-footer {
    padding: 10px;
    .btn {
        min-width: @btn-sm-min-width;
    }
}
.modal-class {
    width: @modal-sm;
    .modal-body {
        height: 500px;
        overflow-y: auto;
    }
}
.sino-modal-default {
    .modal-body {
        overflow-y: auto;
    }
}
.sino-modal-default-xs {
    width: @sino-modal-sm;
    .modal-body {
        height: 100px;
        overflow-y: auto;
    }
}
.sino-modal-default-lg {
    width: 1100px;
    .sino-box-modal {
        max-height: 600px;
        overflow-y: auto;
    }
}


// Modal sizes
.message-default {
    .modal-body {
        padding: 30px;
    }
    .message-text {
        overflow: hidden;
        .fa {
            font-size: 50px;
        }
        span {
            height: 50px;
            line-height: 50px;
            font-size: @font-size-14;
        }
    }
}
