//
// forms
// --------------------------------------------------
// 模块：表单
// 描述：表单模块处理
// --------------------------------------------------

.sino-form {
    padding: @sino-form-padding;
    border: 1px solid @sino-form-border;
    background-color: @sino-form-bg-color;
    &:extend(.clearfix all);

    &.sino-form-nopd {
        padding: 0 !important;
    }
    &.sino-form-nobg {
        background-color: transparent !important;
    }
    &.sino-form-noborder {
        border: 0;
    }
    &.sino-form-dashed {
        border-style: dashed;
        padding: 10px 20px;
        p {
            margin-bottom: 0;
        }
    }
    &.sino-form-title {
        padding-top: 8px;
        padding-bottom: 8px;
    }
    &.sino-form-wrap {
        padding: 0;
        border: 0;
        background-color: transparent !important;
    }

    .sino-form {
        .sino-form-group,
        .sino-form-group-btn {
            margin-left: 0;
            margin-right: 0;
        }
    }
    +.sino-form {
        border-top: 0;
    }

    .sino-form-group {
        .input-group {
            .input-group-addon {
                cursor: pointer;
            }
            &.input-group-select-addon {
                display: block;
                .input-group-addon {
                    line-height: 19px;
                    display: block;
                    float: left;
                }
                @media (min-width: @screen-sm-min) {
                    .bootstrap-select.form-control:not([class*="col-"]) {
                        width: 85.40%;
                    }
                    .input-group-addon {
                        width: 14.60%;
                    }
                }
                @media (min-width: @screen-md-min) {
                    .bootstrap-select.form-control:not([class*="col-"]) {
                        width: 86.75%;
                    }
                    .input-group-addon {
                        width: 13.25%;
                    }
                }
                @media (min-width: @screen-lg-min) {
                    .bootstrap-select.form-control:not([class*="col-"]) {
                        width: 88.95%;
                    }
                    .input-group-addon {
                        width: 11.05%;
                    }
                }
            }
        }
        .input-group-calendar {
            .input-group-addon {
                padding: @padding-base-vertical @padding-base-horizontal;
            }
            .input-sm {
                padding-left: 5px;
                padding-right: 0;
            }
            .control-label {
                text-align: center;
            }
        }
        .input-addon-calendar-back {
            border-right: 0;
            .box-shadow(inset 0 0 0 rgba(0,0,0,.075));
            &:focus {
                border-color: @input-border;;
            }
        }
        .input-addon-calendar,
        .input-addon-edit {
            //.box-shadow(inset 0 0 0 rgba(0,0,0,.075));
            &::-ms-clear {
                display: none;
            }
            &:focus {
                border-color: @input-border-focus;
            }
            +.input-group-addon {
                border-left-color: transparent;
            }
        }
        .input-addon-calendar {
            border-right: 0;
        }
        .input-group-addon-clear + .input-group-addon {
            border-left-color: transparent;
            border-radius: 0 @input-border-radius @input-border-radius 0;
        }

    }

    .sino-form-group-btn {
        padding: @sino-form-btn-padding;
        .btn {
            margin-left: 5px;
            margin-right: 5px;
        }
    }

    .sino-form-group-btn-single {
        @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
            padding-left: (@sino-form-btn-single-padding / 2);
        }
        @media (min-width: @screen-md-min) {
            padding-left: @sino-form-btn-single-padding;
        }
        .btn {
            margin-right: 5px;
            @media (min-width: @screen-md-min) {
                min-width: @btn-min-width;
            }
        }
    }

    .sino-form-group-btn-multiple {
        @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
            padding-left: (@sino-form-btn-single-padding / 2);
        }
        @media (min-width: @screen-md-min) {
            padding-left: @sino-form-btn-single-padding;
        }
        .btn {
            margin-right: 5px;
            &.btn-sm {
                min-height: @btn-min-height-multiple;
                @media (min-width: @screen-md-min) {
                    min-width: @btn-min-width;
                }
            }
            > i {
                display: block;
                margin-top: 4px;
                margin-bottom: 6px;
                font-size: @font-size-18;
            }
        }
    }
    @media (max-width: @screen-xs-max) {
        .sino-form-group-btn-single,
        .sino-form-group-btn-multiple {
            padding-left: 0;
        }
        .sino-form-group-btn-multiple {
            .btn {
                margin-right: 0;
                &.btn-sm {
                    min-height: inherit;
                }
                > i {
                    display: inline-block;
                    margin-top: 0;
                    margin-bottom: 0;
                    font-size: @font-size-12;
                }
            }
        }
    }

    .sino-form-group-horizontal {
        > .control-label {
            @media (min-width: @screen-sm-min) {
                width: 11.1%;
            }
            > span.text-label {
                display: block;
                padding-right: 15px;
                color: lighten(@gray-base, 60%);
            }
        }
        > .control-label + div,
        > .control-label + div.col-sm-11 {
            @media (min-width: @screen-sm-min) {
                width: 88.9%;
            }
            textarea.form-control {
                width: 100%;
            }
            span.text-count{
                position:absolute;
                right: 6px;
                bottom: 6px;
                .text-count-word{
                    color: @brand-danger;
                    padding: 0 4px;
                }
            }
        }
        > .control-label + div.col-sm-10 {
            @media (min-width: @screen-sm-min) {
                width: 77.8%;
                float: left;
            }
        }
        > .control-label + div.col-sm-10 + div.col-sm-1 {
            @media (min-width: @screen-sm-min) {
                width: 11.1%;
                padding-left: 10px;
            }
        }
        > .control-label + div.col-sm-10.col-sm-10-main {
            @media (min-width: @screen-sm-min) {
                width: 77.8%;
                width: ~"-webkit-calc(88.9% - 70px)";
                width: ~"-moz-calc(88.9% - 70px)";
                width: ~"calc(88.9% - 70px)";
                float: left;
            }
        }
        > .control-label + div.col-sm-10 + div.col-sm-1.col-sm-1-side {
            @media (min-width: @screen-sm-min) {
                width: 11.1%;
                width: 60px;
                padding-left: 10px;
            }
        }
    }

    .dropdown-menu {
        .navbar-main > .dropdown-menu();

        width: 100%;
        margin-top: -1px;
        padding: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border: 1px solid @dropdown-fallback-border;
        .nav-tabs {
            width: 15% !important;
        }
        .tab-content {
            width: 85% !important;
        }
        .menu-content {
            width: 100% !important;
        }
    }

    .breadcrumb {
        margin-bottom: 0;
        > li {
            position: relative;
        }
        .dropdown-menu {
            width: auto;
            min-width: 160px;
        }
    }

    .sino-form-show-hide {
        display: none;
    }

    .help-block {
        margin-bottom: 0;
    }

    .sino-make-grid-columns()
}

.sino-form-modal {
    padding: @sino-form-modal-padding;
    border: 1px solid @sino-form-modal-border;
    background-color: @sino-form-modal-bg-color;
}

.tabs-group {
    .tabs-item {
        display: inline-block;
        margin-top: 1px;
        padding: 3px 10px;
        border-radius: 3px;
        border: 1px solid @input-border;
        cursor: pointer;
        +.tabs-item {
            margin-left: 5px;
        }
        &.active {
            background-color: @brand-primary;
            border-color: @brand-primary;
            color: @white;
        }
    }
}

.form-control {
    &.input-borderno {
        background-color: transparent;
        border-color: transparent;
        box-shadow: none;
    }
}

.sino-make-grid-columns() {
    // Common styles for all sizes of grid columns, widths 1-12
    .col(@index) { // initial
        @item: ~".sino-form-group .col-xs-@{index}, .sino-form-group .col-sm-@{index}, .sino-form-group .col-md-@{index}, .sino-form-group .col-lg-@{index}";
        .col((@index + 1), @item);
    }
    .col(@index, @list) when (@index =< @grid-columns) { // general; "=<" isn't a typo
        @item: ~".sino-form-group .col-xs-@{index}, .sino-form-group .col-sm-@{index}, .sino-form-group .col-md-@{index}, .sino-form-group .col-lg-@{index}";
        .col((@index + 1), ~"@{list}, @{item}");
    }
    .col(@index, @list) when (@index > @grid-columns) { // terminal
        @{list} {
            position: relative;
            // Prevent columns from collapsing when empty
            min-height: 1px;
            // Inner gutter via padding
            padding-left:  0;
            padding-right: 0;
        }
    }
    .col(1); // kickstart it
}
