//
// Tables
// --------------------------------------------------

// Baseline styles

.table {
    // Cells
    > thead,
    > tbody,
    > tfoot {
        > tr {
            > th,
            > td {
                font-size: @font-size-12;
                vertical-align: middle;
                .radio, .checkbox {
                    margin-top: 0;
                    margin-bottom: 0;
                    &.checkbox-row1 {
                        height: 20px;
                        overflow: hidden;
                    }
                }
                p {
                    margin-bottom: 0;
                }
            }
            > th {
                padding: @table-cell-th-padding;
                text-align: center;
            }
        }
    }
    > thead > tr {
        background-color: @table-cell-th-default-bg-color;
        #gradient > .vertical(@start-color: @table-cell-th-start-color; @end-color: @table-cell-th-end-color; @start-percent: 0%; @end-percent: 100%);
        &.bg-default {
            background-image: none;
        }
        &.bg-primary {
            background-color: @brand-primary;
            background-image: none;
        }
    }
    // Bottom align for column headings
    > thead > tr > th {
        vertical-align: middle;
        border-bottom: 1px solid @table-border-color;
        padding:@table-cell-padding;
    }
    // Remove top border from thead by default
    > caption + thead,
    > colgroup + thead,
    > thead:first-child {
        > tr:first-child {
            > th,
            > td {
            }
        }
    }
    // Account for multiple tbody instances
    > tbody + tbody {
        border-top: 1px solid @table-border-color;
    }
    &.table-thead {
        > thead > tr {
            &.bg-primary {
                > th {
                    border: 1px solid @brand-primary !important;
                }
            }
            > th {
                border: 1px solid @table-border-color !important;
            }
        }
        > tbody > tr > td {
            &.bg-default,
            &:first-child {
                background-color: @table-bg-hover;
            }
            &.bg-white {
                background-color: @white;
            }
        }
    }
    &.table-borderno {
        > thead > tr {
            background-color: transparent;
            > th {
                border-bottom: 0;
            }
        }
        > tbody > tr > td {
            border-top: 0;
        }
    }
}
