//
// Buttons
// --------------------------------------------------
// 模块：按钮
// 描述：思诺博按钮定义
// --------------------------------------------------

.btn:not(.dropdown-toggle) {
    min-width: @btn-min-width;
    > i {
        margin-right: 10px;
    }
    &.btn-sm {
        min-width: @btn-sm-min-width;
        padding-top: 3px;
        padding-bottom: 3px;
        > i {
            margin-right: 5px;
        }
    }
    &.btn-md {
        padding: 5px 10px;
    }
    &.btn-pd {
        padding-left: 20px;
        padding-right: 20px;
    }
}

// 按钮 minxins
// --------------------------------------------------

.button-variant(@color; @background; @border) {
    color: @color;
    background-color: @background;
    border-color: @border;

    &:focus,
    &.focus {
        color: @color;
        background-color: darken(@background, 5%);
        border-color: darken(@border, 5%);
    }
    &:hover {
        color: @color;
        background-color: darken(@background, 5%);
        border-color: darken(@border, 5%);
    }
    &:active,
    &.active,
    .open > .dropdown-toggle& {
        color: @color;
        background-color: darken(@background, 5%);
        border-color: darken(@border, 5%);

        &:hover,
        &:focus,
        &.focus {
            color: @color;
            background-color: darken(@background, 5%);
            border-color: darken(@border, 5%);
        }
    }
}

.btn-group{
    >.btn{
        +.btn.dropdown-toggle{
            min-width: inherit;
            border-left: 1px solid transparent;
            &.btn-default {
                border-left-color: darken(@btn-default-border, 5%);
            }
            &.btn-primary {
                border-left-color: darken(@btn-primary-border, 5%);
            }
            &.btn-success {
                border-left-color: darken(@btn-success-border, 5%);
            }
            &.btn-info {
                border-left-color: darken(@btn-info-border, 5%);
            }
            &.btn-warning {
                border-left-color: darken(@btn-warning-border, 5%);
            }
            &.btn-danger {
                border-left-color: darken(@btn-danger-border, 5%);
            }
        }
    }
}

// 边框按钮
.btn-primary-bd {
    .button-variant(@btn-primary-border; @white; @btn-primary-border);
}
.btn-info-bd {
    .button-variant(@btn-info-border; @white; @btn-info-border);
}

.link-group {
    a {
        &.text-white {
            color: @white;
            &:hover {
                color: @white;
            }
        }
        + a {
            margin-left: 10px;
        }
    }
    &.link-group-block {
        a {
            display: block;
        }
    }
}
