//
// footer
// --------------------------------------------------
// 模块：底部
// 类名：footer
// 描述：页面底部
// --------------------------------------------------

.footer {
    min-height: @footer-min-height;
    padding-top: @footer-padding-top;
    padding-bottom: @footer-padding-bottom;
    border-top: @footer-border-top solid @footer-border-top-color;
    margin-top: @footer-margin-top;
    @media (max-width: @screen-xs-max) {
        margin-top: (@footer-margin-top / 2);
    }
    #gradient > .directional(@start-color: @footer-start-color; @end-color: @footer-end-color; @deg: 45deg);
}
