// Grid
// --------------------------------------------------
// 模块：栅格
// 描述：视窗小于1200px时，宽度100%自适应
// --------------------------------------------------

.container {
    @media (min-width: @screen-sm-min) {
        .width-auto();
    }
    @media (min-width: @screen-md-min) {
        .width-auto();
    }
    @media (min-width: @screen-lg-min) {
        .width-auto();
    }
    // Grid 升级（ sinoui-grid.less & v0.5.8+）
    @media (min-width: @screen-bg-min) {
        .width-auto-big();
        margin:0 40px;
    }
}

.width-auto {
  width: auto;
  max-width: @container-lg;
}
.width-auto-big {
    width: auto;
    max-width: @container-bg;
}

.sino-container {
    .sino-side {
        &.sino-side-hide {
            display: none;
        }
    }
    .sino-main {
        @media (max-width: @screen-xs-max) {
            padding-left: 0;
        }
        .row {
            margin-left: 0;

            ul.nav-tabs-title:first-child {
                margin-top: 0;
            }
        }
        .col-sm-11.col-md-11 {
            width: 88.5%;
            @media (min-width: @screen-sm-min) {
                width: ~'-moz-calc(100% - 100px)';
                width: ~'-webkit-calc(100% - 100px)';
                width: ~'calc(100% - 100px)';
            }
        }
        .col-sm-1.col-md-1 {
            width: 100px;
        }
        &.sino-main-full{
            width: 100%!important;
        }
    }
}
