//
// --------------------------------------------------
// 模块：布局
// 类名：layout
// --------------------------------------------------
.layout {
    position: relative;
    .layout-width();
    .flexbox();
    .align-items(stretch);
    .layout-left {
        position: relative;
        width: 220px;
        .hairline(right, @nav-tabs-border-color);
    }
    .layout-right {
        width: ~"-webkit-calc(100% - 220px)";
        width: ~"-moz-calc(100% - 220px)";
        width: ~"calc(100% - 220px)";
        padding-left: 20px;
    }
}

.layout-box {
    position: relative;
    .layout-width();
    .flexbox();
    .align-items(stretch);
    &.layout-box-border {
        border: 1px solid @layout-border-color;
    }
    &+.layout-box {
        border-top-width: 0;
    }
    .layout-left {
        position: relative;
        width: 246px;
        z-index: 1;
        .hairline(right, @nav-tabs-border-color);
        &.layout-left-sm {
            width: 34px;
            .db-search,
            .brand-title i.fa,
            .db-datalist .brand-box {
                display: none;
            }
            .brand-title {
                width: 120px;
                height: 36px;
                clear: both;
                .icon {
                    margin-right: 0;
                    margin-left: 8px;
                    font-size: @font-size-18;
                    float: left;
                }
                span {
                    float: left;
                    margin-left: 12px;
                    margin-top: 1px;
                    display: none;
                }
                &:hover {
                    background-color: @gray-lighter;
                    border-radius: 3px;
                    span {
                        display: block;
                    }
                }
            }
        }
    }
    .layout-right {
        width: ~"-webkit-calc(100% - 246px)";
        width: ~"-moz-calc(100% - 246px)";
        width: ~"calc(100% - 246px)";
    }
    .layout-right-lg {
        width: ~"-webkit-calc(100% - 34px)";
        width: ~"-moz-calc(100% - 34px)";
        width: ~"calc(100% - 34px)";
        > .mr-20 {
            margin-right: 0;
        }
    }
    .box-title {
        .flexbox();
        .align-items(center);
        .icon {
            font-size: @font-size-18;
        }
    }
    .layout-toggle-bar {
        height: 30px;
        .icon {
            width: 30px;
            height: 30px;
            line-height: 30px;
            font-size: 24px;
            cursor: pointer;
            color: @gray-light;
            &:hover {
                color: @gray-dark;
            }
            &.icon-outdent.icon-indent {
                margin-right: 5px;
                &:before {
                    content: "\e7f3";
                }
            }
        }
    }
}

.footer-block {
    height: 77px;
}
.footer-fixed {
    position: fixed;
    left: 0;
    bottom: 27px;
    right: 0;
    height: 50px;
    z-index: 999;
    background-color: @white;
    .box-shadow(inset 0 1px 1px rgba(0,0,0,.1));
    .footer-inner {
        padding: 10px 0;
        .layout-width();
        .flexbox();
        .align-items(center);
        .justify-content(flex-end);
        .btn+.btn {
            margin-left: 10px;
        }
    }
}

.layout-width() {
    margin-right: auto;
    margin-left: auto;
    &:extend(.clearfix all);
    @media (min-width: @screen-sm-min) {
        .width-auto();
    }
    @media (min-width: @screen-md-min) {
        .width-auto();
    }
    @media (min-width: @screen-lg-min) {
        .width-auto();
    }
    @media (min-width: @screen-bg-min) {
        .width-auto-big();
        margin:0 40px;
    }
}

.width-auto {
    width: auto;
    max-width: @container-lg;
}
.width-auto-big {
    width: auto;
    max-width: @container-bg;
}

.layout-data {
    position: relative;
    height: 100%;
    padding-top: 40px;
    background-color: @graylight;
    .flexbox();
    .align-items(stretch);
    &:extend(.clearfix all);
    .layout-topbar {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 40px;
        background-color: @white;
        .hairline(bottom, @nav-tabs-border-color);
        .flexbox();
        .justify-content(space-between);
        .topbar-left {
            span {
                position: relative;
                display: inline-block;
                float: left;
                height: 40px;
                line-height: 40px;
                padding: 0 30px;
                background-color: @graylight;
                color: @brand-primary;
                .hairline(right, @nav-tabs-border-color);
            }
        }
        .topbar-right {
            padding-top: 6px;
            padding-right: 8px;
        }
    }
    .layout-left {
        position: relative;
        width: 180px;
        .hairline(right, @nav-tabs-border-color);
    }
    .layout-center {
        position: relative;
        width: 220px;
        .hairline(right, @nav-tabs-border-color);
    }
    .layout-right {
        width: ~"-webkit-calc(100% - 400px)";
        width: ~"-moz-calc(100% - 400px)";
        width: ~"calc(100% - 400px)";
    }
}
